<template>
  <div class="in-views staff">
    <transition name="intoleft" appear>
      <h2 class="heading2">
        <transition name="intoleft" mode="out-in" appear>
          <p class="sub">スタッフ</p>
          </transition>
          <p>Staff</p>
        </h2>
        
      </transition>
      
      <!-- スタッフ一覧全体を包むdiv -->
      <div class="staff-list">
        <!-- スタッフ一覧をv-forで回す -->
        <template v-for="portrait in portraits">
          <!-- スタッフ個別の表示域 -->
          <div :key="portrait.staffId" class="staff-area">
            <!-- 写真以外の要素を分ける -->
            <div class="staff-prof-wrap">
              <!-- スタッフ名表示域 -->
              <div class="staff-name-area">
                <!-- スタッフ名フリガナ -->
                  <p class="staff-name-sub">
                    {{portrait.staffNameSub}}
                  </p>
                <!-- フリガナと名前・役職を分ける -->
                <div class="staff-name-wrap">
                  <!-- スタッフ名 -->
                  <p class="staff-name">
                    {{portrait.staffName}}
                  </p>
                
                <!-- スタッフ役職 -->
                <p class="staff-position">
                  {{portrait.staffPosition}}
                </p>
                </div>
              </div>
              <!-- スタッフ経歴 -->
              <p class="staff-prof">
                {{portrait.staffProf}}
              </p>
            </div>
            <!-- スタッフ写真 -->
            <img class="port-staff" :src="portrait.staffPic">
          </div>
        </template>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Staff',
  data() {
    return{
      portraits: [
        {
          staffId: 1,
          staffName: 'Jaden Sutherland',
          staffPosition: '制作総指揮／監督',
          staffNameSub: 'ジェイデン・サザーランド',
          staffPic: require('@/assets/pic_staff_01.png'),
          staffProf: '1960年、カナダ・ハリファックス生まれ。父は脚本家、母は舞台女優という芸能一家に生まれ、子役としてハリウッドやフランスで数多くの映画に出演。86年には二本の作品でヴェネチア国際映画祭の男優賞受賞。89年、ガーザ・ベラスケス監督の『Twice』で初めて製作を務め、米アカデミー賞外国語映画賞を受賞。80本以上の映画に出演し、また数多くの長編映画で製作を担当している。99年、『An Animals』で動物の世界に目覚め、『ワールド』（04）、『マイグレーション』（08）で製作を務めた。さらに『コロイド』（13）では、製作およびロバート・ミラー、タラ・ローレンスとの共同監督を務めた。その他製作作品に、『無限のコーラス』（15）、『ペリカン』（18）などがある。ロバート・ミラー監督と共同監督を務めた『One』（20） では、権威あるセザール賞ドキュメンタリー賞を受賞。',
        },
        {
          staffId: 2,
          staffName: 'Robert Miller',
          staffPosition: '監督',
          staffNameSub: 'ロバート・ミラー',
          staffPic: require('@/assets/pic_staff_02.png'),
          staffProf: '1985年パリ第8大学映画科を卒業。90年から01年までフィクション映画の第一助監督として、多くの作品を担当し、フィクション短編映画も多く監督した。その後、数多くのドキュメンタリー作品やCM、特撮映像の監督などを経て、13年、ジェイデン・サザーランド、タラ・ローレンスと『コロイド』（13）で共同監督を務め、「コロイド　～もうひとつの願い～」（13/TV）でも監督を務めた。15年から18年にかけて、ジャック・ペランと『シンフォニー』をダブルIMAX仕様で共同監督、その後『One』(20)の脚本共同執筆と海洋、海中撮影用の特殊技術の共同開発に携わり、セザール賞ドキュメンタリー賞を受賞。',
        },
      ]
    }
  },
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@200&display=swap');

.staff{
  display: flex;
  flex-flow: column;
}

.staff-list{
  display: flex;
  width: 100%;
  flex-flow: column;
  padding-top: 8.5vh;
}

.staff-area{
  display: flex;
  flex-wrap: wrap;
  height: auto;
  margin: 4vh 0;
}

.staff-prof-wrap{
  width: 70%;
  display: flex;
  flex-flow: column;
  padding: 0 1vw;
}

.staff-name-area{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.staff-name-wrap{
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}

.staff-name{
  font-size: 2.5rem;
  width: auto;
  display: flex;
  justify-content: flex-start;
}

.staff-name-sub{
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.staff-position{
  display: flex;
  width: 50%;
  height: auto;
  font-size: 1.8rem;
  flex-grow: 1;
  align-items: center;
  padding-left: 2rem;
  justify-content: flex-start;
  line-height: 2.3;
  
}

.staff-prof{
  font-family: 'Noto Serif JP', serif;
  display: flex;
  width: auto;
  height: auto;
  font-size: 1.2rem;
  font-weight: 200;
  flex-grow: 2;
  justify-content: center;
  align-items: center;
  text-align: justify;
}

.port-staff{
  width: 30%;
  height: auto;
  object-fit: contain;
  padding: 10px;
}

@media screen and (max-width:1200px){
  .staff-name{
    font-size: 3vw;
  }

  .staff-name-sub{
    font-size: 1.8vw;
  }

  .staff-position{
    font-size: 2vw;
    line-height: 2.5;
  }

  .staff-prof{
    font-size: 1.4vw;
  }
}

@media screen and (max-width:600px){
  .staff-list{
    padding-top: 15vh;
  }
  .staff-prof{
    font-size: 1rem;
  }

  .port-staff{
    display: block;
    width: 50%;
    order: -1;
    margin-top: 7.5vh;
    align-self: baseline;
  }

  .staff-prof-wrap{
    width: 50%;
  }

  .staff-name-area{
    width: 200%;
    margin-left: -100%;
  }

  .staff-name{
    font-size: 1.8rem;
    
  }

  .staff-name-sub{
    font-size: 1rem;
  }

  .staff-position{
    font-size: 1.2rem;
  }
}
</style>
